.Aside {
  padding: 20px 20px 20px 0;
  margin-top: 20px; }
  .Aside ul {
    margin-left: 20px; }
    .Aside ul li {
      list-style-type: circle;
      margin: 10px 5px;
      font-size: 1rem; }
      .Aside ul li:first-of-type {
        font-weight: bold;
        font-size: 1.2rem;
        list-style-type: none; }

@media (max-width: 599px) {
  .Aside {
    margin-top: 0;
    padding-top: 0; }
    .Aside ul {
      display: -webkit-box;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
              flex-flow: row wrap; }
      .Aside ul li {
        margin: 0;
        padding: 5px 10px;
        list-style-type: none;
        border-right: 1px solid black; }
        .Aside ul li:last-child {
          border-right: none; } }
