.NavMobile {
  background-color: #D5D4E6;
  display: none;
  position: relative; }
  .NavMobile .NavMenu {
    background-color: #D5D4E6;
    position: absolute;
    top: 100%;
    -webkit-transform-origin: 100% 0%;
            transform-origin: 100% 0%;
    width: 100%;
    z-index: 100; }
    .NavMobile .NavMenu ul {
      margin: 0 25px; }
    .NavMobile .NavMenu li:not(:first-child) {
      border-top: 1px solid #efefef; }
  .NavMobile .Active {
    color: #d8b093; }

@media (max-width: 768px) {
  .NavMobile {
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: end;
            justify-content: flex-end; } }

.Show {
  -webkit-transition: all 1s;
  transition: all 1s;
  opacity: 1;
  -webkit-transform: scaleY(1);
          transform: scaleY(1); }

.Hide {
  -webkit-transition: all 1s;
  transition: all 1s;
  opacity: 0;
  -webkit-transform: scaleY(0);
          transform: scaleY(0); }
