.NotActive {
  color: #efefef;
  text-shadow: 1px 1px 5px #000;
  text-decoration: none; }
  .NotActive:hover {
    color: #FFFFFF; }

.Active {
  color: #9996cd;
  text-shadow: 1px 1px 1px #000;
  text-decoration: none; }
  .Active:hover {
    color: #9996cd; }

.Active, .NotActive {
  display: -webkit-box;
  display: flex;
  font-family: 'Trebuchet MS',Arial, sans-serif;
  font-size: 1.1rem;
  padding: 10px 5px;
  margin: 5px auto;
  text-align: center;
  text-transform: uppercase; }
