.NavigationBtn {
  background: transparent;
  border: 1px solid transparent;
  font-size: 14px;
  height: 25px;
  padding: 0;
  position: relative;
  width: 25px;
  cursor: pointer; }
  .NavigationBtn:focus {
    outline-color: #d8b093; }
  .NavigationBtn span {
    background: #383838;
    height: 2px;
    left: 50%;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-transition: background 0.4s;
    transition: background 0.4s;
    width: 22px;
    z-index: 500px; }
    .NavigationBtn span::before, .NavigationBtn span::after {
      content: "";
      background: #383838;
      height: 2px;
      left: 50%;
      position: absolute;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      -webkit-transition: top 0.5s, left 0.5s, -webkit-transform 0.5s;
      transition: top 0.5s, left 0.5s, -webkit-transform 0.5s;
      transition: top 0.5s, left 0.5s, transform 0.5s;
      transition: top 0.5s, left 0.5s, transform 0.5s, -webkit-transform 0.5s;
      width: 22px; }
    .NavigationBtn span::before {
      top: 8px; }
    .NavigationBtn span::after {
      top: -8px; }

.Active span {
  background: rgba(0, 0, 0, 0); }
  .Active span::before {
    left: 0;
    top: 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  .Active span::after {
    left: 0;
    top: 0;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg); }
